.weui_btn.weui_btn_mini {
  line-height: 1.9;
  font-size: 14px;
  padding: 0 .75em;
  display: inline-block;
}
button.weui_btn,
input.weui_btn {
  width: 100%;
  border-width: 0;
  outline: 0;
  -webkit-appearance: none;
}
button.weui_btn:focus,
input.weui_btn:focus {
  outline: 0;
}
button.weui_btn_inline,
input.weui_btn_inline,
button.weui_btn_mini,
input.weui_btn_mini {
  width: auto;
}
/*gap between btn*/
.weui_btn + .weui_btn {
  margin-top: 15px;
}
.weui_btn.weui_btn_inline + .weui_btn.weui_btn_inline {
  margin-top: auto;
  margin-left: 15px;
}
.weui_btn_area {
  margin: 1.17647059em 15px 0.3em;
}
.weui_btn_area.weui_btn_area_inline {
  display: -webkit-box;
  display: -webkit-flex;
  display: flex;
}
.weui_btn_area.weui_btn_area_inline .weui_btn {
  margin-top: auto;
  margin-right: 15px;
  width: 100%;
  -webkit-box-flex: 1;
  -webkit-flex: 1;
          flex: 1;
}
.weui_btn_area.weui_btn_area_inline .weui_btn:last-child {
  margin-right: 0;
}
.weui_btn {
  position: relative;
  display: block;
  margin-left: auto;
  margin-right: auto;
  padding-left: 14px;
  padding-right: 14px;
  box-sizing: border-box;
  font-size: 18px;
  text-align: center;
  text-decoration: none;
  color: #FFFFFF;
  line-height: 2.33333333;
  border-radius: 5px;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  overflow: hidden;
}
.weui_btn:after {
  content: " ";
  width: 200%;
  height: 200%;
  position: absolute;
  top: 0;
  left: 0;
  border: 1px solid rgba(0, 0, 0, 0.2);
  -webkit-transform: scale(0.5);
          transform: scale(0.5);
  -webkit-transform-origin: 0 0;
          transform-origin: 0 0;
  box-sizing: border-box;
  border-radius: 10px;
}
.weui_btn.weui_btn_inline {
  display: inline-block;
}
.weui_btn_default {
  background-color: #F7F7F7;
  color: #454545;
}
.weui_btn_default:not(.weui_btn_disabled):visited {
  color: #454545;
}
.weui_btn_default:not(.weui_btn_disabled):active {
  color: #A1A1A1;
  background-color: #DEDEDE;
}
.weui_btn_primary {
  background-color: #04BE02;
}
.weui_btn_primary:not(.weui_btn_disabled):visited {
  color: #FFFFFF;
}
.weui_btn_primary:not(.weui_btn_disabled):active {
  color: rgba(255, 255, 255, 0.4);
  background-color: #039702;
}
.weui_btn_warn {
  background-color: #EF4F4F;
}
.weui_btn_warn:not(.weui_btn_disabled):visited {
  color: #FFFFFF;
}
.weui_btn_warn:not(.weui_btn_disabled):active {
  color: rgba(255, 255, 255, 0.4);
  background-color: #C13E3E;
}
.weui_btn_disabled {
  color: rgba(255, 255, 255, 0.6);
}
.weui_btn_disabled.weui_btn_default {
  color: #C9C9C9;
}
.weui_btn_plain_primary {
  color: #04BE02;
  border: 1px solid #04BE02;
}
button.weui_btn_plain_primary,
input.weui_btn_plain_primary {
  border-width: 1px;
  background-color: transparent;
}
.weui_btn_plain_primary:active {
  border-color: #039702;
}
.weui_btn_plain_primary:after {
  border-width: 0;
}
.weui_btn_plain_default {
  color: #5A5A5A;
  border: 1px solid #5A5A5A;
}
button.weui_btn_plain_default,
input.weui_btn_plain_default {
  border-width: 1px;
  background-color: transparent;
}
.weui_btn_plain_default:after {
  border-width: 0;
}



